import React from "react";
import SEO from "../components/seo";
import openPositions from "../components/Careers/kernel.json";
import { getSlug } from "../components/Common/utils/getSlug.js";
import { Link } from "react-router-dom";

class Kernel extends React.Component {
  getCurrentWidth = () => {
    let screenWidth = null;
    if (typeof window !== `undefined`) {
      screenWidth = window.screen.width;
    }
    return screenWidth;
  };

  getCurrentHeight = () => {
    let screenHeight = null;
    if (typeof window !== `undefined`) {
      screenHeight = window.screen.height;
    }
    return screenHeight;
  };
  render() {
    const { data, singapore } = openPositions;
    return (
      <section className="first-section">
        <SEO
          isDynamic={false}
          title="Gojek: The Kernel Team"
          description="The the Kernel team is responsible for running the cloud-native platform that powers Gojek's engineering ecosystem."
          url="https://gojek.io"
        />
        {/* <div className="container-fluid"> */}
        {/* <div className="row"> */}
        {/* <div className="col-md-5 p-md-0 p-5">
              <h1 className="maison-bold text-center text-white">
                Identity &amp; Communication Platform (ICP)
              </h1>
            </div> */}
        <div className="p-0">
          <div className="bg-dark kernel-banner go-jek-work-gradient relative-position" />
        </div>
        {/* </div> */}
        {/* </div> */}
        <div className="container mt-5">
          <h2 className="text-center maison-bold mb-4">The Kernel Team</h2>
          <p className="description-font text-dark">
            Gojek is a Super App: one app with over 20 services including food
            delivery, commuting, digital payments, shopping, hyper-local
            delivery, massages, and many more. Gojek is 11th in Fortune’s list
            of “50 companies that changed the world” in 2019. It does this by
            enabling 2M+ driver-partners, 500k+ merchant partners, and 60k+
            service partners across the sub-continent and making us Indonesia’s
            de-facto transportation choice. Gojek is a verb! Gojek is a way of
            life!
          </p>
          <p className="description-font text-dark">
            <b>The Kernel Team</b> - In a nutshell, the Kernel team is
            responsible for running the cloud-native platform that powers
            Gojek's engineering ecosystem.
          </p>
          <p className="description-font text-dark">
            We work on creating &amp; standardizing distributed system
            abstractions over an evolving PaaS that puts engineering
            effectiveness first. Our goal is to help Gojek's product teams
            surpass engineering goals of reliability, stability, speed so they
            can deliver business impact predictably, efficiently, and at scale.
          </p>
          <p className="description-font text-dark">
            Just like Gojek is the operating system for SEA markets, Kernel aims
            to be the operating system for Gojek.
          </p>
          <p className="font-weight-bold font-sm">
            What does it translate to in terms of the work Kernel team members
            are doing? A few examples:
          </p>
          <ul className="description-font text-dark ml-3">
            <li>Self-Serve Platform as a Service</li>
            <li>Infra Architecture Redesign &amp; Org-wide Migration</li>
            <li>Uptime Tracking and Standardised Monitoring &amp; Alerting</li>
            <li>Highly Available Component Management</li>
            <li>Deployment Strategies Automation</li>
            <li>Networking, Identity &amp; Team Protocols</li>
          </ul>
          <p className="font-weight-bold font-sm my-3">Our Tech Stack:</p>

          <ul className="description-font text-dark ml-3">
            <li>Go, Java, Clojure, RoR</li>
            <li>Shell programming, Linux, Terraform, Kafka</li>
            <li>RabbitMQ, Kubernetes, Helm, GCP</li>
            <li>Grafana, Prometheus, JavaScript, Kong</li>
          </ul>

          <p className="description-font text-dark pt-3">
            We are very open to trying out new technology if it solves our
            business problems most efficiently.
          </p>

          <div className="row mx-0">
            {/* Open Positions */}
            <div>
              <p className="description-font text-dark">
                Intrigued by what you’ve read so far and want to work with us?
                Check out our current openings across various locations:
              </p>

              <h5>Bangalore</h5>
              <div className="row mx-0 pb-2">
                {data.map((job, i) => {
                  return (
                    <div
                      id={job.id}
                      name={job.id}
                      key={i}
                      className="col-12 col-md-6 my-2 mb-0 px-2 col-lg-4"
                    >
                      {/* eslint-disable-next-line */}
                      <Link
                        target="_blank"
                        to={{
                          pathname: `/all-open-positions`,
                          search: `?d=${getSlug(
                            job.categories.department
                          )}&t=${getSlug(job.categories.team)}&p=${job.id}`,
                          state: { jobSelected: job }
                        }}
                        key={i}
                        // onClick={() => this.onClickPositionFromSearch(data)}
                        // className="text-left bg-white-hover-gray border-0 py-2 col-12 scroll"
                      >
                        <div
                          style={{
                            minHeight:
                              this.getCurrentWidth() >= 768 ? "95px" : "90px",
                            borderRadius: "10px"
                          }}
                          className={` scroll  d-flex flex-column flex-wrap justify-content-center careers-position careers-position-unhighlight`}
                        >
                          {/* <Link to={{}} className=""> */}
                          <h6 className=" px-4 roboto-regular font-sm text-dark ">
                            {job.text}
                          </h6>
                          <div className="d-flex flex-row flex-wrap roboto-black font-xs text-uppercase">
                            <span className="pl-4  text-green  col-">
                              {job.categories.location}
                            </span>
                            <span className="pl-4  text-black  col-">
                              {job.categories.team}
                            </span>
                          </div>
                          {/* </Link> */}
                          {this.props.location.search.split("=")[3] ===
                            job.id && (
                            <div>
                              {
                                <div className="pl-4 pt-3">
                                  <div className="row justify-content-between align-items-center pl-4" />
                                  <div className="d-block text-center" />
                                </div>
                              }
                            </div>
                          )}
                        </div>
                      </Link>
                    </div>
                  );
                })}
              </div>
              <h5>Jakarta</h5>
              <div className="row mx-0 pb-5">
                {singapore.map((job, i) => {
                  return (
                    <div
                      id={job.id}
                      name={job.id}
                      key={i}
                      className="col-12 col-md-6 my-2 mb-0 px-2 col-lg-4"
                    >
                      {/* eslint-disable-next-line */}
                      <Link
                        target="_blank"
                        to={{
                          pathname: `/all-open-positions`,
                          search: `?d=${getSlug(
                            job.categories.department
                          )}&t=${getSlug(job.categories.team)}&p=${job.id}`,
                          state: { jobSelected: job }
                        }}
                        key={i}
                        // onClick={() => this.onClickPositionFromSearch(data)}
                        // className="text-left bg-white-hover-gray border-0 py-2 col-12 scroll"
                      >
                        <div
                          style={{
                            minHeight:
                              this.getCurrentWidth() >= 768 ? "95px" : "90px",
                            borderRadius: "10px"
                          }}
                          className={` scroll  d-flex flex-column flex-wrap justify-content-center careers-position careers-position-unhighlight`}
                        >
                          {/* <Link to={{}} className=""> */}
                          <h6 className=" px-4 roboto-regular font-sm text-dark ">
                            {job.text}
                          </h6>
                          <div className="d-flex flex-row flex-wrap roboto-black font-xs text-uppercase">
                            <span className="pl-4  text-green  col-">
                              {job.categories.location}
                            </span>
                            <span className="pl-4  text-black  col-">
                              {job.categories.team}
                            </span>
                          </div>
                          {/* </Link> */}
                          {this.props.location.search.split("=")[3] ===
                            job.id && (
                            <div>
                              {
                                <div className="pl-4 pt-3">
                                  <div className="row justify-content-between align-items-center pl-4" />
                                  <div className="d-block text-center" />
                                </div>
                              }
                            </div>
                          )}
                        </div>
                      </Link>
                    </div>
                  );
                })}
              </div>
            </div>
            {/* End Open positions */}
          </div>
        </div>
      </section>
    );
  }
}
export default Kernel;



